import React, { FunctionComponent } from "react";
import StyledMenu from "./Menu.styled";

const Menu: FunctionComponent<{
  open: boolean,
  setOpen: (val: boolean) => void,
  setPage: (val: string) => void
}> = ({ open, setOpen, setPage }) => {
  const isHidden = !!open;
  const tabIndex = isHidden ? 0 : -1;

  const tabComponent = (name: string, icon: string) => (
    <button
      type="button"
      onClick={() => {
        setPage(name);
        setOpen(false);
      }}
      tabIndex={tabIndex}
    >
      <span aria-hidden="true">{icon}</span>
      {name}
    </button>
  );

  return (
    <StyledMenu open={open} aria-hidden={!isHidden}>
      {tabComponent("Home", "💁🏻‍♂️")}
      {tabComponent("Camera", "📷")}
      {tabComponent("Map", "🗺")}
    </StyledMenu>
  );
};

export default Menu;
