import React, { FunctionComponent, useState } from "react";
import {
  Map, Marker, Popup, TileLayer
} from "react-leaflet";
import L from "leaflet";
import "leaflet/dist/leaflet.css";

// https://github.com/PaulLeCam/react-leaflet/issues/453#issuecomment-611930767
L.Icon.Default.imagePath = "./";

const MapComponent: FunctionComponent = () => {
  const [state, setState] = useState<{
    popUpText: string,
    center: [number, number]
  }>({
    popUpText: "Latitude: 1.2796562, Longitude: 103.8164022",
    center: [1.2796562, 103.8164022]
  });

  const getLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setState({
            popUpText: `Latitude: ${position.coords.latitude}, Longitude: ${position.coords.longitude}`,
            center: [position.coords.latitude, position.coords.longitude]
          });
        },
        (error) => console.log(error.message),
        { enableHighAccuracy: true, timeout: 20000, maximumAge: 1000 }
      );
    }
  };
  return (
    <div className="page">
      <h3>Geolocation Example</h3>
      <Map
        data-testid="map"
        viewport={{ center: state.center, zoom: 15 }}
        minZoom={12}
        maxZoom={15}
        zoomControl={false}
      >
        <TileLayer
          // eslint-disable-next-line max-len
          attribution='<img src="https://docs.onemap.sg/maps/images/oneMap64-01.png" style="height:20px;width:20px;"/> New OneMap | Map data &copy; contributors, <a href="http://SLA.gov.sg">Singapore Land Authority</a>'
          url="https://maps-{s}.onemap.sg/v3/Default/{z}/{x}/{y}.png"
        />
        <Marker position={state.center}>
          <Popup>{state.popUpText}</Popup>
        </Marker>
      </Map>
      <button type="button" onClick={getLocation}>
        Get My Location
      </button>
    </div>
  );
};

export default MapComponent;
