import React, { FunctionComponent, useEffect } from "react";
import StyledCameraFeed from "./Camera.styled";

const Camera: FunctionComponent = () => {
  useEffect(() => {
    let vidStream: MediaStream;
    const video = (document.getElementById("video") as HTMLVideoElement);
    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
      navigator.mediaDevices.getUserMedia({ video: true })
        .then((stream) => {
          vidStream = stream;
          if (video) {
            video.srcObject = stream;
            video.play();
          }
        });
    }
    return () => {
      if (vidStream) {
        vidStream.getTracks().forEach((track) => {
          track.stop();
        });
      }
    };
  }, []);

  return (
    <div className="page">
      <h3>Camera Example</h3>
      <StyledCameraFeed data-testid="camera-canvas" id="video" />
    </div>
  );
};

export default Camera;
